<template> <!-- le code à afficher à la place de la balise </App/> -->
  <h2 id="test"> Galerie d'images présentes sur le serveur </h2>

    
          <div id="tab"></div>
    

</template>

<script> //contient le code javascript

import axios from "axios";
export default {
  name: "",
  props: {
    msg: String,
  },
  data() {
    return {
      response: [],
      errors: [],
      file: ''
    };
  },


 methods: {
    callRestService() {
      
      axios
        .get(`images`)
        .then((response) => {
          // JSON responses are automatically parsed.
          this.response = response.data;
          console.log(response.data);
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
    createOneTable(i){
        return "<tr><td> " + 
                   '<img src="http://localhost:8080/images/'+i+
                   '"> </td>';
    },
    createGalery(){
        axios
        .get(`images`)
        .then((response) => {
          // JSON responses are automatically parsed.
          this.response = response.data;
            let rep="<table>"
            let i=0;
            for (var x in response.data) {
                console.log(x);
                rep=rep+this.createOneTable(i);
                i++;
            }
            rep=rep+"</table>"
            document.getElementById("tab").innerHTML=rep;
        })
        .catch((e) => {
          this.errors.push(e);
        });
    },
    

  },
  mounted(){
      this.callRestService();
      this.createGalery();
  }
};
</script>

